import React, { useState, useEffect } from 'react';
import "./MyPage.css";
import myprofile_image from '../../img/mypage/userprofile.png'
import {useAuth} from "../Login/AuthProvider";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
// react-toastify  css
import 'react-toastify/dist/ReactToastify.css';

function MyPage() {
    const [profileImage, setProfileImage] = useState(myprofile_image); //   
    const [ageGroup, setAgeGroup] = useState(""); //  
    const [category, setCategory] = useState(""); // 
    const [name, setName] = useState("");
    const [nickname, setNickname] = useState("");
    const [introduce, setIntroduce] = useState(""); //   

    const [isEditing, setIsEditing] = useState(false); //  

    const [message, setMessage] = useState(""); // 

    //    
    const [isNameTooLong, setIsNameTooLong] = useState(false);
    const [isNicknameTooLong, setIsNicknameTooLong] = useState(false);
    const [isIntroduceTooLong, setIsIntroduceTooLong] = useState(false);
    // useAuth     .
    // const { token } = useAuth();
    const auth = useAuth();

    //   
    const handleNameChange = (e) => {
        const value = e.target.value;
        setIsNameTooLong(value.length > 10);
        setName(value);
    };

    const handleNicknameChange = (e) => {
        const value = e.target.value;
        setIsNicknameTooLong(value.length > 10);
        setNickname(value);
    };

    const handleIntroduceChange = (e) => {
        const value = e.target.value;
        setIsIntroduceTooLong(value.length > 35);
        setIntroduce(value);
    };


    const handleButtonClick = () => {
        if (isEditing) { //  ''   

            // let jwt = localStorage.getItem("jwt");
            // let data1 = parseJwt(jwt);
            // let memberId = data1.sub;
            let memberId = auth.userId;
            if( !memberId ) return;

            let data = JSON.stringify({
                "member_id": memberId,
                "member_name": name,
                "member_nickname": nickname,
                "member_introduce": introduce,
                "ageGroup": ageGroup,
                "category_name": category
            });

            let config = {
                method: 'post',
                maxBodyLength: Infinity,
                url: 'http://localhost:8080/mypage/updateInfo',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${localStorage.getItem("jwt")}`
                },
                data : data
            };

            axios.request(config)
                .then((response) => {
                    console.log(JSON.stringify(response.data));
                })
                .catch((error) => {
                    console.log(error);
                });

            toast.success("");
        } else { // ''   
            setMessage(""); //  
        }
        setIsEditing(prev => !prev); //   
    }



    useEffect(() => {
        console.log("--------- My Page -------------------------------")
        // let jwt = localStorage.getItem("jwt");
        // if( jwt == undefined) return;
        // let data = parseJwt(jwt);
        // let memberId = data.sub;
        // let memberId = auth.userId;
        // console.log('auth.userId', memberId);
        let memberId = localStorage.getItem("memberId");
        console.log('memberId', memberId);
        if( !memberId ) return;
        console.log("----------------------------------------")
        console.log(memberId);
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `http://localhost:8080/mypage/${memberId}/detail`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${localStorage.getItem("jwt")}`
            },
            data : memberId
        };

        axios.request(config)
            .then((response) => {
                console.log(response.data);
                setName(response.data.member_name);
                setNickname(response.data.member_nickname);
                setIntroduce(response.data.member_introduce);
                setAgeGroup(response.data.ageGroup);
                setCategory(response.data.category_name);
                // setProfileImage("/uploadimg/"+response.data.member_profile_image);
                setProfileImage("http://localhost:8080/uploadimg/"+response.data.member_profile_image); //   
            })
            .catch((error) => {
                console.log(error);
            });



    }, []);



    const handleImageChange = (e) => {
        console.log('==========================================222');
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setProfileImage(reader.result); //     .
            }
            reader.readAsDataURL(file); //   data URL .
        }
        let memberId = auth.userId;
        if( !memberId ) return;

        //   
        let data = new FormData();
        data.append("member_id", memberId);
        data.append('profileImage', file);

        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: 'http://localhost:8080/mypage/uploadProfileImage',
            headers: {
                'Authorization': `Bearer ${localStorage.getItem("jwt")}`,
                'Content-Type': 'multipart/form-data'
            },
            data : data
        };

        axios.request(config)
            .then((response) => {
                console.log(JSON.stringify(response.data));
                if(response.data.success == 'success') {
                    let filename = response.data.data;
                    // let imageUrl = '/uploadimg/' + filename;
                    let imageUrl = 'http://localhost:8080/uploadimg/' + filename; //   
                    setProfileImage(imageUrl);
                }
            })
            .catch((error) => {
                console.log(error);
            });

    };

    const handleAgeGroupChange = (event) => {
        setAgeGroup(event.target.value);
    }

    const handleCategoryChange = (event) => {
        setCategory(event.target.value);
    }




    return (
        <div className="myPage">
            <ToastContainer
                position="top-right"
                limit={1}
                closeButton={false}
                autoClose={3000}
                className="custom-toast-container"
                toastClassName="custom-toast"
            />
            <h2 id="nickname"> </h2>
            <div className="infoSection">
                <div className="profile_image">
                    <img src={profileImage} alt="profile"/>
                    <input type="file" onChange={handleImageChange} id="image_uploadInput" />
                </div>


                <div className="inputContainer">

                    <div className="input_text">
                        
                    </div>

                    <div
                        className="inputWrap"
                    >
                        <input
                            className="input"
                            type="text"
                            value={name}
                            readOnly={!isEditing} // isEditing false  readOnly 
                            /*onChange={e => setName(e.target.value)}*/
                            onChange={handleNameChange}
                        />

                    </div>

                    <div>
                        {isNameTooLong && <span className="error-message" >  . 10  .</span>}
                    </div>

                    <div className="input_text">
                        
                    </div>

                    <div
                        className="inputWrap"
                    >
                        <input
                            className="input"
                            type="text"
                            value={nickname}
                            readOnly={!isEditing} // isEditing false  readOnly 
                            /*onChange={e => setNickname(e.target.value)}*/
                            onChange={handleNicknameChange}
                        />
                    </div>
                    <div>
                        {isNicknameTooLong&& <span className="error-message">  . 10   </span>}

                    </div>

                    <div className="input_text">
                        
                    </div>

                    <div
                        className="inputWrap"
                    >
                        <input
                            className="input"
                            type="text"
                            value={introduce}
                            readOnly={!isEditing} // isEditing false  readOnly 
                            /*onChange={e => setIntroduce(e.target.value)}*/
                            onChange={handleIntroduceChange}
                        />
                    </div>
                    <div>
                        {isIntroduceTooLong && <span className="error-message" >  . 35  .</span>}
                    </div>

                    <div className="input_text">
                        
                    </div>


                    <form className={`age-group-form ${ageGroup}`}>
                        <label className="radio">
                            <input
                                type="radio"
                                value="10s"
                                checked={ageGroup === '10s'}
                                onChange={handleAgeGroupChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;10
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="20s"
                                checked={ageGroup === '20s'}
                                onChange={handleAgeGroupChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;20
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="30s"
                                checked={ageGroup === '30s'}
                                onChange={handleAgeGroupChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;30
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="40s-50s"
                                checked={ageGroup === '40s-50s'}
                                onChange={handleAgeGroupChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;40-50
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="s60_PLUS"
                                checked={ageGroup === 's60_PLUS'}
                                onChange={handleAgeGroupChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;60 
                        </label>
                    </form>

                    <div className="input_text">
                           
                    </div>

                    <form className={`category-form ${category}`}>
                        <label className="radio">
                            <input
                                type="radio"
                                value="culture"
                                checked={category === 'culture'}
                                onChange={handleCategoryChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="harmony"
                                checked={category === 'harmony'}
                                onChange={handleCategoryChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="traditional"
                                checked={category === 'traditional'}
                                onChange={handleCategoryChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="local_specialty"
                                checked={category === 'local_specialty'}
                                onChange={handleCategoryChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;
                        </label>
                        <label className="radio">
                            <input
                                type="radio"
                                value="nature"
                                checked={category === 'nature'}
                                onChange={handleCategoryChange}
                                disabled={!isEditing} // isEditing false  disabled 
                            />
                            &nbsp;
                        </label>
                    </form>

                </div>
            </div>

            {!isEditing && (
                <>
                    <div className="buttonsSection">
                        <button id="likedEvents"></button>
                        <button id="myPosts">  </button>
                    </div>
                    <div id="contentContainer" className="contentContainer">


                    </div>

                </>
            )}
            <div className="modifySection">
                <button id="modifyInfo" onClick={handleButtonClick}
                        disabled={isNameTooLong || isNicknameTooLong || isIntroduceTooLong}
                        style={{
                            backgroundColor: (isNameTooLong || isNicknameTooLong || isIntroduceTooLong) ? 'grey' : '#EC7373'
                        }}
                >{isEditing ? '' : ''}</button>

                {/*     */}
                {!isEditing && (
                    <>
                        <button id="modifyPassword"><a href="http://localhost:8080/change_pw/"
                                                       rel="noopener noreferrer"> </a></button>
                        <button id="withdraw"><a href="http://localhost:8080/delete/"
                                                 rel="noopener noreferrer"></a></button>
                    </>
                )}
            </div>
        </div>
    );
}

export default MyPage;